import React from 'react'
import Button from '@mui/material/Button';

const CartAmountToggle = ({amount,setDecrease , setIncrease}) => {
  return (
    <div className='toggle_container'>
    <div className='toggle_btn'style={{display:'flex',justifyContent:'space-between',width:'25%',marginTop:'3rem'}} >
    <div className='minus' onClick={() => setDecrease()}> <i class="fa-solid fa-minus"></i> </div>
    <div className='amount' style={{padding:'0 2rem'}}>{amount}</div>
    <div className='add' onClick={() => setIncrease()}> <i class="fa-solid fa-plus"></i> </div>
    </div>

    
    </div>
  ) 
} 

export default CartAmountToggle