import React from 'react'
import { useFilterContext } from './FilterContext'

const Sort = () => {
  const { grid_view , setGridView,setListView,filter_products, sorting} = useFilterContext();
 
  return (
   <div className='sort_items' style={{display:'flex',justifyContent:'space-around',marginBottom:'4rem'}} >
  <div className='sort_icons'>

     
     <i className={grid_view ?"fa-solid fa-grip icong active" :"fa-solid fa-grip icong"  } 
      onClick={setGridView}>
      </i>
    

     <i className={!grid_view ?"fa-solid fa-grip icong active" :"fa-solid fa-grip icong"}
     onClick={setListView} ></i>

    </div>

    <div className='total_products'>

        <p style={{fontSize:'2rem'}}>{`${filter_products.length} products available`}</p>
        
    </div>
    <div className='sort_selection'>
      <form action='#'>
        <label htmlFor='sort'></label>
          <select name='sort' id='sort' 
           onClick={sorting}>
        <option value='lowest'>price(lowest)</option>        
        <option value='highest'>price(highest)</option>
        <option value='a-z'>price(a-z)</option>
        <option value='z-a'>price(z-a)</option>

        </select>
      </form>
    </div>
   </div>
  )
}

export default Sort