import { useState } from "react"
import { UseProductContext } from "./Productcontext";
import DeleteIcon from '@mui/icons-material/Delete';
import { useCartContext } from "./CartContext";
import CartAmountToggle from "./CartAmountToggle";


const CartItem = ({ id, name, image, color, price, amount }) =>{
    const { removeItem , setIncrease ,setDecrease } = useCartContext()
    
    const { singleProduct, } = UseProductContext()
    const {stock} = singleProduct

//   const [ amount , setamount] = useState(1)

//     const setDecrease =() => {
//         amount > 1 ? setamount(amount - 1) : setamount(1) 
//       }
//       const setIncrease =() => {
//         amount < stock ? setamount(amount + 1) : setamount(stock) 
//       }

    return(
        <div className="grid_five_column">
            <div className="cart_image">
                <div  style={{display:'flex'}}>
                    <figure>
                        <img src={image} alt="" />
                    </figure>
                    <div className="color_div">
                    <p style={{fontWeight:500,color:'blue'}}>  {name}</p> 

                    <p style={{display:'flex'}}>
                    color

                    <div  className="button_style" style={{backgroundColor:color,marginLeft:'.6rem'}} ></div> 
                    </p>
                      
                    </div>
                </div>
            </div>
            
            {/* price */}
            <div className="cart_hide">
                <p>{price}</p>
            </div>

            {/* quantity */}
            <CartAmountToggle 
          amount={amount}   
          setIncrease={() => setIncrease(id)}
          setDecrease={() => setDecrease(id)}
            />


    {/* Subtotal */}
    <div className ='cart_hide'>
    <p>{price * amount}</p>
     </div>

     <div>

     <DeleteIcon onClick={() =>removeItem(id)} style={{fontSize:'3rem',color:'red',cursor:'pointer'}}/>
     </div>
    </div>
    
    )
}

export default CartItem