import React, { useState } from 'react'

const Image = ({imgs = [{url: ''}]}) => {
    // console.log(imgs)
    const [mainImage , setMainImage] = useState(imgs[0])
        // setMainImage(imgs[0].url)
    return(
        <>
        <div className='main_image'>
        <img src= {mainImage.url} alt='mainimg' className='big_img'></img>
        </div>

   

        <div className='small_img_container' style={{display:'flex',justifyContent:'space-between'}}>

         {
            imgs.map((curElem,index) => {
                return(

                    <div className='small_image'>
                     
                    <img src={curElem.url} alt={curElem.filename} 
                        onClick={() => setMainImage(curElem)}
                    />
                    </div>

                )
            }
            )
 }</div>


        
        {/* my main screen image */}
        
       </> 
        )
 
}

export default Image