  import React from 'react'
import { useFilterContext } from './FilterContext'
import ProductList from './ProductList'
import Sort from './Sort'
import FilterSection from './FilterSection'
const Products = () => {
  const {filter_products} = useFilterContext();
  // console.log(filter_products);
  
  return (
    <div className='products_container' style={{display:'flex',flexDirection:'column'}}>
    <div className='left_column' style={{display:'grid',gridTemplateColumns:'40rem 180rem'}}>
      <div className='fiter_Section' style={{textAlign:'left'}}>
        {/* <input type='search'>search</input> */}
       <FilterSection/>
       </div>
      

      <div className='right_column' style={{display:'flex',flexDirection:'column',flexWrap:'wrap'}} >
      <Sort />

      
      <div className='products_items'>
      <ProductList />  
      </div>
      </div>
      </div>

    </div>
  )
}

export default Products