import { Button } from '@material-ui/core'
import React, { useState } from 'react'
import img from './img/r3.webp'
import { NavLink, useNavigate } from 'react-router-dom'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import axios from 'axios'
const Register = () => {
  const navigate = useNavigate()
const [info , setInfo ] = useState({
  name: "",
  email: "",
  phone: "",
  password: "",
  cpassword: "",
})

const handleInput = (e) => {
  const value = e.target.value
  const name = e.target.name
  
   setInfo((preInfo) => {
    return{
    ...preInfo,
   [name] : value
}})
}

const handleSubmit = async (e) => {
  try {
    
  e.preventDefault()
  const {name , email,phone , password , cpassword} = info;
  // console.log(name,email,password,cpassword)
  const res = await axios.post('/api/register' ,{
    name,email,phone,password,cpassword
 })
 console.log(res)
 console.log(res.data.message)

 if (res && res.data.success) {
  toast.success(res.data && res.data.message);
  navigate("/login");
} else {
  toast.error(res.data.message);
}
  } catch (error) {
    console.log(error)
    toast.error("eror in registeration")
  }
}

  return (
    <div className='form_container'>
    <div className='form_page'>
      <form onSubmit={handleSubmit}>
      <div className='input_field'>
      <h1 style={{marginBottom:"10rem",fontSize:"5rem",fontWeight:"bolder"}}>signup</h1>

        <div>
        <label><i class="fa-solid fa-user"></i></label>
          <input type='text' placeholder='your name' onChange={handleInput} value={info.name} name='name'  required></input>
        </div>

        <div>
        <label><i class="fa-solid fa-envelope" ></i></label>
          <input type='email' placeholder='your email' onChange={handleInput} value={info.email} name='email' required></input>
        </div>

        <div>
        <label><i class="fa-solid fa-phone" ></i></label>
          <input type='number' placeholder='your phone' onChange={handleInput} value={info.phone} name='phone' required></input>
        </div>

        <div>
        <label><i class="fa-solid fa-lock" ></i></label>
          <input type='password' placeholder=' password' onChange={handleInput} value={info.password} name='password' required></input>
        </div>

        <div>
        <label><i class="fa-solid fa-unlock-keyhole"></i></label>
          <input type='password' placeholder='confirm password' onChange={handleInput} value={info.cpassword} name='cpassword' required></input>
        </div>

        </div>

        <Button type='submit' variant="contained" style={{padding:' 1rem 3rem',background:"crimson",color:'white',fontSize:'2.5rem'}}>register</Button>

      </form>

      <div className='img_container'>
        <figure>
          <img src={img} alt='piccc' style={{width:'65rem',height:'100%'}}></img>
         
        </figure>
        <NavLink to="/LogIn">
            <h2 style={{textAlign:'center',marginTop:'6rem', fontSize:"2.1rem"}}> i am already Register</h2>
          </NavLink>
      </div>
            
      </div>
      <ToastContainer style={{fontSize:'3rem'}}/>

    </div>
  )
}

export default Register