import { useEffect } from "react";
import { UseProductContext } from "./Productcontext";
import { useParams } from 'react-router-dom';
import { PageNavigation } from "./PageNavigation";
import Image from "./Image";
import AddtoCart from "./AddtoCart";
import { NavLink } from "react-router-dom";


const API = "https://api.pujakaitem.com/api/products";


const SingleProduct = () => {
  const {isSingleLoading, singleProduct, getSingleProduct} = UseProductContext()
  // console.log(singleProduct)

  const { id }  = useParams()
  // console.log(id)
 
const {
  id:alias,
  name,
  company,
  price,
  description,
  category,
  stock,
  stars,
  image,
  reviews
} = singleProduct;
// console.log(image)


  useEffect(()  =>{
   getSingleProduct(`${API}?id=${id}`)
}, [])                                                  


  if(isSingleLoading){
    return <h2>loading.....</h2>
  }
  return  (
    <>
        <PageNavigation title={name} />
        <div className="single_container"  style={{display:'flex' , justifyContent:'center'}}>

            {/* product images */}
            <div className="left" style={{display:"flex",flexDirection:'column'}}>
            <Image 
              imgs ={image}/>
             
            </div>
             
            {/* product data */}
       <div className='product_data' >
            <h1>{name}</h1>
            <p> {stars}   </p>
            <p>{reviews}reviews</p>

            <div className="product_price">
              MRP:
              <del style= {{textDecoration: 'line-through'}}>
               {price + 20000}
              </del>

            <p className="product_real_price">
              deal of the Day: {price}
            </p>
            </div> 

             <p>{description}</p>
             
             <div className="product_waranty_data">

              <div className="waranty_icon">
              <i className="fa-solid fa-truck "></i>
                <p>free delivery</p>
              </div>

              <div className="waranty_icon">
              <i className="fa-solid fa-reply-all"></i>
               <p>30 days replacement</p>
              </div>
              
              <div className="waranty_icon">
              <i className="fa-solid fa-truck"></i>
              <p>sadeed deliverd</p>
              </div>

              <div className="waranty_icon">
              <i className="fa-solid fa-shield-halved"></i>
              <p>1 year waranty</p>
              </div>
             </div>        <hr style={{background:'grey',opacity:'.6'}}></hr>

             
             <div className="product_data_info">
              <p> available: {stock > 0 ? <span>in Stock</span> : "not availabel"} </p>
             </div>
             
             <p> id: <span>{id}</span></p>
             <p>brand: <span>{company}</span></p>
            <hr style={{ border: '.1rem solid black' ,background:'black'}}></hr>              
            
            {stock > 0 && <AddtoCart  product={singleProduct} /> }
</div>
      
        </div>
   </>
  )
}

export default SingleProduct