const cartReducer = (state, action) => {
   if(action.type === 'ADD_TO_CART'){
    let { id , color, amount, product} = action.payload;
    // console.log(product)
    // takle existing products
          var existingProduct =  state.cart?.find((curElem) =>  
          (curElem.id == id + color) 
         )

        //  console.log(existingProduct)

         if(existingProduct){
                let updatedProduct = state.cart.map((curElem) => {
                    if(curElem.id == id + color){
                        let newAmount = curElem.amount + amount;
                        return{
                            ...curElem,
                            amount : newAmount
                        }
                    }else{
                        return curElem
                    }
                    
                }) 
            return{
                ...state,
                cart : updatedProduct
            }
         }
        else{
            let cartProduct;

            cartProduct ={
                id: id + color,
                name: product.name,
                color,
                amount,
                image: product.image[0].url,
                price:product.price,
                max:product.stock,
            }
            
            return {
                ...state,
                cart : [...state?.cart, cartProduct]
               }
           
        
         }
 
         }
           
   if(action.type === 'REMOVE_ITEM'){
        let remaining_item = state.cart.filter((curElem) => {
        return curElem.id !== action.payload
 })


  return {
    ...state,
    cart :remaining_item
   }
   }
   

   if(action.type === 'CLEAR_CART'){
    return {
        ...state,
        cart :[]
       }
   }

   // to set the increment and decrement
   if (action.type === "SET_DECREMENT") {
    let updatedProduct = state.cart.map((curElem) => {
      if (curElem.id === action.payload) {
        let decAmount = curElem.amount - 1;

        if (decAmount <= 1) {
          decAmount = 1;
        }

        return {
          ...curElem,
          amount: decAmount,
        };
      } else {
        return curElem;
      }
    });
    return { ...state, cart: updatedProduct };
  }

  if (action.type === "SET_INCREMENT") {
    let updatedProduct = state.cart.map((curElem) => {
      if (curElem.id === action.payload) {
        let incAmount = curElem.amount +  1;

        if (incAmount >= curElem.max) {
          incAmount = curElem.max;
        }
        
        return {
          ...curElem,
          amount: incAmount,
        };
      } else {
        return curElem;
      }
    });
    return { ...state, cart: updatedProduct };
  }

  if(action.type === "GET_TOTAL_ITEM"){
    let updatedValue = state.cart?.reduce((accum, curElm)  => {
     accum = accum + curElm.amount;
      return accum
    },0);

    return{
      ...state,
      total_item:updatedValue
     }
  }

  if(action.type === "CART_TOTAL_PRICE"){
   let totalPrice = state.cart?.reduce((accum , curElm) => {
    const {price , amount } = curElm
    accum = accum + price * amount;
    return accum
   }, 0)
       return{
        ...state,
        totalPrice:totalPrice
       }
} 
  
}
export default  cartReducer