import React from 'react';
import { useFilterContext } from './FilterContext';
import { Button } from '@material-ui/core';

const FilterSection = () => {
  const {
    filters: { text , color ,maxPrice , minPrice , price },
    updateFilterValue,
    all_products,
    reset
   
  
  } = useFilterContext();
//console.log(maxPrice)
  //to need the unique data of each filed
  const getUniqueData = (data, attr) => {
   let newVal = data.map((curElem) => {
    // console.log(curElem[attr])
    return curElem[attr]
   })
   if(attr === 'colors'){
    newVal = ['All',  ...new Set([].concat(...newVal))]     
    //  console.log(newVal)
    return newVal
   }else{
      newVal = [ "All" , ...new Set(newVal)]
    //  console.log(newVal)
     return newVal
   }

  }
  const getCompanyData = (data, company) => {
   var newVal =    data.map((curElm) => {
    return curElm.company
 })
 if(newVal){
 newVal = [ 'all', ...new Set(newVal)]
}
    return newVal

  }
  //we need unique data 
  const categoryOnlyData =  getUniqueData(all_products, "category")
  const companyOnlyData = getCompanyData(all_products , "company")
  // console.log(companyOnlyData)
  const colorsOnlyData = getUniqueData(all_products , "colors")
  const priceOnlyData = getUniqueData(all_products , "price")

//  console.log(priceOnlyData)

  return (
    <div className='filter_seacrh'>
        <form onSubmit={(e) => e.preventDefault()}>
            <input type='text' 
                   name= 'text'
                   placeholder='search Products'
                   value = {text}
                   onChange={updateFilterValue}>
                   </input>
        </form> 

        <div className='filter_category'>
          <h3 className='filter_heading'> category</h3>
          {
           categoryOnlyData.map((curElem,index ) => {
            return(
              <button 
              key={index}
              type='text'
              name='category'
              value={curElem}
              // className={curElem === category ? "active" : ""}
             onClick ={updateFilterValue}>
             {curElem}
              </button>
            )
           })
          }
      
       </div>

       {/* <div className="filter-category">
        <h3 className='filter_heading'>Category</h3>
        <div>
          {categoryOnlyData.map((curElem, index) => {
            return (
              <button
                key={index}
                type="button"
                name="category"
                value={curElem}
                className={curElem === category ? "active" : ""}
                onClick={updateFilterValue}>
                {curElem}
              </button>
            );
          })}
        </div>
      </div> */}

       
       <div className='filter_company'>
        <h3 className='filter_heading'>company</h3>
          <form action='#'>
              <select name='company'
                      onClick={updateFilterValue}
                       className='select_option'>
                      {
                        companyOnlyData.map((curElem,i) => {
                          return(
                            <option 
                            key={i}
                            name='company'
                            value={curElem}>
                              {curElem}
                            </option>
                          )
                          
                        })
                      }
                      </select>
          </form>
        </div>


        <div classname='colors_filter'> 
          <h3 className='filter_heading'> colors</h3>
          <div className='colors' style={{display:'flex'}} >


        
          {                 
            colorsOnlyData.map((curColor) => {
            if(curColor === "all"){
              return(
                <button
                type='text'
                name='color'
                className='all_btn'
                value={curColor}
                onClick={updateFilterValue}>
                All
                </button>

              )
            }
              
              return(
                <button
                type='text'
                style={{background:curColor}}

                name='color'
                value={curColor}
                className={ color === curColor? 'button active' : 'button'} 
                onClick={updateFilterValue}>
                {color === curColor ? <i class="fa-solid fa-check"> </i>: null }
                </button>

              )
            })
          }
          </div>
      
        </div>
         <div className='price_filter'>
         <h3 className='filter_heading'> price </h3>
         <p>{price}</p>
           <input 
           type='range'
           min={minPrice}
           max={maxPrice}
           value={price}
           name="price"
           onChange={updateFilterValue}
           >
           </input>
         </div>

         <div className='clear_btn ' >
         <Button variant="contained"
         onClick={reset}>clear All</Button>
         </div>
         
    </div>

    
  )

  }
export default FilterSection
