import { UseProductContext } from "./Productcontext";
import Product from "./Product";
import { useContext } from "react";


const FeatureProduct = () => {
    const { isLoading, featureProducts } = UseProductContext();
  //  console.log(featureProducts)

      if(isLoading){
        return <div style={{fontSize:'5rem',textAlign:'center'}}>.....loading</div> 
      }
      return(
        <>
        <div className="section">
            <div className="container">
                <div className="intro_data">cheackNow!</div>
                <div className="common_heading">our feature services </div>
            </div>

            <div className='card_box'>

            {featureProducts.map((currelm) => {
               return <Product  key={currelm.id} {...currelm}   />
            })}
</div>
            </div>
        </>
      )
}

export default FeatureProduct
