import React from 'react'
import { NavLink } from "react-router-dom"
import { useState } from 'react';
import ShoppingBasketIcon from '@mui/icons-material/ShoppingBasket';
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';
import Badge from '@mui/material/Badge';
import MailIcon from '@mui/icons-material/Mail';
import { useCartContext } from './CartContext';


const Navbar = () => {
  const {total_item} = useCartContext()

  const [menu, setmenu] = useState(false)
  const show = () =>{
     setmenu(!menu)
    //  console.log('clik')
  }
  return (
    <div className='navbar'>
        <div className='logo'>Sadeed shop
          <ShoppingBasketIcon className='icon'/>
        </div>
       <div className= {menu ? "links active" : 'links'}>
        <NavLink to = '/' className='link'>home</NavLink>
        <NavLink to = '/about' className='link'>about</NavLink>
        <NavLink to = '/products' className='link'>products</NavLink>
        <NavLink to = '/contact' className='link'>contact</NavLink>
         <NavLink to = '/register' className='link'>register</NavLink>

        <NavLink to = '/cart'>
        {/* <AddShoppingCartIcon   className='icon'/> */}
         <Badge badgeContent={total_item} color="primary">
      <AddShoppingCartIcon color="action"  className='icon' />
      
    </Badge>
    cart
        </NavLink>
        
        </div>
        <i className="fa-solid fa-bars" onClick={show}></i>


    </div>  
   )
}

export default Navbar