import { Button } from '@material-ui/core'
import React, { useState } from 'react'
import { NavLink, useNavigate } from 'react-router-dom'
 import img from './img/r1.webp'
import axios from 'axios'
import { ToastContainer, toast } from 'react-toastify'
// import img from '.img/r1.webp';
const LogIn = () => {
 const [info , setInfo ] = useState({
  email:"",
  password:"",
 })
     const navigate = useNavigate()
     const handleInput = (e) => {
      const value =e.target.value;
      const name = e.target.name;

      setInfo((pre) => {
        return {
        ...pre,
        [name]:value
      }
      })

     }

     const handleSubmit = async (e) => {
      e.preventDefault();
      try {
        const {email , password } = info
        const res = await axios.post("/api/login", {
          email,
          password,
        });
        console.log(res)
        console.log(res.data.message)
        if (res && res.data.success) {
          toast.success("login suceefully")
          toast.success(res.data && res.data.message);
        
          navigate( "/");
        } else {
          toast.error(res.data.message);
        }
      } catch (error) {
        console.log(error);
        toast.error("Something went wrong");
      }
    };
  return (
    <div className='form_container'>
    <div className='form_page'>
      <form onSubmit={handleSubmit}>
      <div className='input_field'>
      <h1 style={{marginBottom:"10rem",fontSize:"5rem",fontWeight:"bolder"}}>login</h1>

        <div>
        <label><i class="fa-solid fa-envelope" ></i></label>
          <input type='email' placeholder='your email' onChange={handleInput} value={info.email} name='email' required></input>
        </div>

        <div>
        <label><i class="fa-solid fa-lock" ></i></label>
          <input type='password' placeholder=' password' onChange={handleInput} value={info.password} name='password' required></input>
        </div>

        </div>

        <Button type='submit' variant="contained" style={{padding:' 1rem 3rem',background:"crimson",color:'white',fontSize:'2.5rem'}}>login</Button>
      </form>

      <div className='img_container'>
        <figure>
          <img src={img} alt='pic' style={{width:'65rem',height:'100%'}}></img>
         
        </figure>
        <NavLink to="/register">
            <h2 style={{textAlign:'center',marginTop:'6rem', fontSize:"2.1rem"}}>  Register</h2>
          </NavLink>
      </div>
                  <ToastContainer />

      </div>
    </div>
  )
}

export default LogIn