import React from 'react'
import { useCartContext } from './CartContext'
import CartItem from './CartItem';
import { NavLink } from 'react-router-dom';
import { Button } from '@mui/material';

const Cart = () => {
  const { cart , clearCart, totalPrice, shippingFee} = useCartContext();

  if(cart?.length === 0){
    return (
      <>
        <div className='empty_cart'>
          <p>no cart item </p>
        </div>
      </>
    )
  }
  else{
    return(
      <>
        <div className='cart_container'>
        
          <div className='card_heading grid_five_column'>
            <p>item</p>
            <p className='cart_hide'>price</p>
            <p>quantity</p>
            <p className='cart_hide'>Subtotal</p>
            <p>remove</p>
  
          </div>
          <hr />
  
             <div className='cart_item'>
              {
                cart?.map((curElm) => {
                  return <CartItem key={curElm.id} {...curElm}/>
                })  
              }
              <hr />
             </div>
             <div className = 'cart_two_button'>
             <NavLink to='/products'>
              <Button variant='contained'>continue shopping</Button>
              </NavLink>
              <Button variant='contained' className='clear' 
               onClick={clearCart}>clear Chart</Button>
             </div>
  
            <div className='sub_total_container'>
            <div className='detail'>
              <p>Subtotal : {totalPrice}</p>
            
              <p>shippingFee: {shippingFee}</p>
              <hr />

              <p>oreder total : {Number (totalPrice) + Number (shippingFee)}</p>
            </div>
            </div>
        </div>
      </>
    )
  
  }
 
  // return (

  // )
}

export default Cart